.card-courser-bg{
    width: calc(270 / 1920 * 100vw);
    height: calc(350 / 1920 * 100vw);
    position: relative;
    border-radius: 20px;
    border: 2px solid var(--main-gray);
    background-color: var(--main-black);
    padding-block: calc(25 / 1920 * 100vw);
    padding-inline: calc(18 / 1920 * 100vw);
    word-wrap: break-word;
    transition-duration: 300ms;
    user-select: none;
}

.card-courser-bg:hover{
    scale: 1.04;
    box-shadow: 9px 9px 14px 13px rgba(0, 0, 0, 0.25);
}

.card-courses-logo{
    position: absolute;
    left: 0;
    bottom: 0;
    margin-inline: calc(18 / 1920 * 100vw);
    margin-block: calc(25 / 1920 * 100vw);
}

.card-courses-header-content{
    margin-bottom: calc(35 / 1920 * 100vw);
}

.card-courses-years-container{
    margin-bottom: calc(20 / 1920 * 100vw);
}

@media (max-width: 1440px) {
    .card-courser-bg{
        margin-inline: calc(5 / 1440 * 100vw);
    }
}

@media (max-width: 820px) {
    .card-courser-bg{
        width: 49%;
        height: calc(350 / 640 * 100vw);
        margin-bottom: calc(20/ 640 * 100vw);
        padding-block: calc(25 / 640 * 100vw);
        padding-inline: calc(18 / 640 * 100vw);
    }
    .card-courses-years-container{
        margin-bottom: calc(20 / 640 * 100vw);
    }

    .card-courses-logo{
        margin-inline: calc(18 / 640 * 100vw);
        margin-block: calc(25 / 640 * 100vw);
    }
    .card-courses-header-content{
        margin-bottom: calc(35 / 640 * 100vw);
    }

}

@media (max-width: 420px) {
    .card-courser-bg{
        position: static;

        height: auto;
        width: 100%;

        padding-block: calc(25 / 420 * 100vw);
        padding-inline: calc(18 / 420 * 100vw);
    }

    .card-courses-logo{
        position: static;
        margin-inline: 0;
        margin-block: calc(25 / 420 * 100vw);
    }

    .card-courses-header-content{
        margin-bottom: calc(35 / 640 * 100vw);
    }

    .card-courses-years-container{
        margin-bottom: calc(20 / 640 * 100vw);
    }

}