.geograph{
    margin-inline: calc(60 / 1920 * 100vw);
    margin-block: calc(20 / 1920 * 100vw);
}

.geograph-map{
    margin-top: calc(35 / 1920 * 100vw);
}

@media (max-width: 1440px) {
    .geograph{
        margin-inline: calc(230 / 1920 * 100vw);
    }
}

@media (max-width: 820px) {
    .geograph{
        margin-inline:0;
    }

    .geograph-map{
        margin-top: calc(35 / 820 * 100vw);
    }
}

@media (max-width: 420px) {
    .geograph{
        margin-inline:0;
    }

    .geograph-map{
        overflow: scroll;
        border-radius: 5px;
    }

    .geograph-map-img{
        height: calc(600 / 420 * 100vw);
        width: max-content;
    }
}