.experience{
    margin-left: calc(60 / 1920 * 100vw);
    margin-top: calc(40 / 1920 * 100vw);
}

@media (max-width: 1440px) {
    .experience{
        margin-left: calc(230 / 1440 * 100vw);
    }
}

@media (max-width: 820px) {
    .experience{
        margin-block: calc(30 / 640 * 100vw);
        margin-inline: 0;
    }
}