.quote-card{
    border: 2px solid var(--main-gray);
    border-radius: 20px;
    width: calc(680 / 1920 * 100vw);
    position: relative;
    transition-duration: 300ms;
    user-select: none;
}

.quote-card:hover{
    box-shadow: 9px 9px 14px 13px rgba(0, 0, 0, 0.25);
    scale: 1.05;
    transition-duration: 300ms;
}

.quote-card__content{
    display: flex;
    align-items: flex-start;
    padding-inline: calc(25 / 1920 * 100vw);
    padding-block: calc(25 / 1920 * 100vw);
}

.quote-card__content-img{
    width: 100%;
    margin-right: calc(20 / 1920 * 100vw);
}
.quote-card__content-text{
    margin-bottom: calc(30 / 1920 * 100vw);
    font-size: calc(18 / 1920 * 100vw);
}

.quote-card__content-author{
    position: absolute;
    bottom: calc(25 / 1920 * 100vw);
    display: flex;
    margin-top: calc(30 / 1920 * 100vw);
}

.quote-card__content-author-p{
    margin-left: calc(16 / 1920 * 100vw);
    font-size: calc(32 / 1920 * 100vw);
    font-weight: bold;
    text-transform: uppercase;
}

@media (max-width: 820px) {
    .quote-card{
        position: static;
        width: 100%;
    }

    .quote-card__content{
        flex-direction: column;
        align-items: center;
        padding-inline: calc(25 / 640 * 100vw);
        padding-block: calc(25 / 640 * 100vw);
    }

    .quote-card__content-img{
        width: calc(120 / 640 * 100vw);
        margin-right: 0;
        margin-bottom: calc(24 / 640 * 100vw);
    }

    .quote-card__content-img img{
        width: calc(120 / 640 * 100vw);
        height: auto;
    }

    .quote-card__content-text{
        margin-bottom: calc(16 / 640 * 100vw);
        font-size: calc(20 / 640 * 100vw);
    }

    .quote-card__content-author{
        position: static;
        margin-top: calc(60 / 640 * 100vw);
    }

    .quote-card__content-author-p{
        font-size: calc(32 / 640 * 100vw);
    }
}

@media (max-width: 420px) {
    .quote-card__content{
        flex-direction: column;
        align-items: center;
        padding-inline: calc(25 / 420 * 100vw);
        padding-block: calc(25 / 420 * 100vw);
    }

    .quote-card__content-img{
        width: calc(120 / 640 * 100vw);
        margin-right: 0;
        margin-bottom: calc(24 / 420 * 100vw);
    }

    .quote-card__content-img img{
        width: calc(120 / 420 * 100vw);
        height: auto;
    }

    .quote-card__content-text{
        margin-bottom: calc(16 / 420 * 100vw);
        font-size: calc(18 / 420 * 100vw);
    }

    .quote-card__content-author{
        position: static;
        margin-top: calc(60 / 420 * 100vw);
    }

    .quote-card__content-author-p{
        margin-left: calc(20 / 420 * 100vw);
        font-size: calc(24 / 420 * 100vw);
    }
}