.card-publication{
    width: calc(390 / 1920 * 100vw);
    border: 2px solid var(--main-gray);
    border-radius: 20px;
    transition-duration: 300ms;
}

.card-publication:hover{
    border: 2px solid var(--main-pink);
    box-shadow: 9px 9px 14px 13px rgba(0, 0, 0, 0.25);
    scale: 1.05;
}

.card-publication:active{
    border: 2px solid var(--main-pink);
    scale: 0.95;
}

.card-publication__content{
    padding-inline: calc(25 / 1920 * 100vw);
    padding-block: calc(18 / 1920 * 100vw);
}

.card-publication__text{
    margin-top: calc(40 / 1920 * 100vw);
}

.card-publication__text h4{
    font-size: calc(24 / 1920 * 100vw);
    font-weight: bold;
    margin-bottom: calc(20 / 1920 * 100vw);
}

.card-publication__text p{
    text-align: justify;
    font-size: calc(16 / 1920 * 100vw);
}

@media (max-width: 820px) {
    .card-publication{
        width: 100%;
    }

    .card-publication__content{
        padding-inline: calc(25 / 640 * 100vw);
        padding-block: calc(18 / 640 * 100vw);
    }

    .card-publication__text{
        margin-top: calc(40 / 640 * 100vw);
    }
    
    .card-publication__text h4{
        font-size: calc(24 / 640 * 100vw);
        margin-bottom: calc(20 / 640 * 100vw);
    }
    
    .card-publication__text p{
        text-align: justify;
        font-size: calc(16 / 640 * 100vw);
    }
    
}

@media (max-width: 420px) {
    .card-publication{
        width: 100%;
    }

    .card-publication__content{
        padding-inline: calc(25 / 420 * 100vw);
        padding-block: calc(18 / 420 * 100vw);
    }

    .card-publication__text{
        margin-top: calc(40 / 420 * 100vw);
    }

    .card-publication__text h4{
        font-size: calc(24 / 429 * 100vw);
        margin-bottom: calc(20 / 420 * 100vw);
    }
    
    .card-publication__text p{
        text-align: justify;
        font-size: calc(16 / 420 * 100vw);
    }
}
